.label-modal {
  &-container {
  }

  &-main-input {
    width: 100%;
    padding: 8px 12px;
    margin: 4px 0 0px;
    //pushes everything down for some reason need to understand
    // margin:4px 0 12px;
  }
}

.edit-modal-labels {
  list-style: none;
  margin-bottom: 8px;
}

.task-label {
  border-radius: 3px;
  cursor: pointer;
  font-weight: 700;
  margin: 0 0 4px;
  min-height: 32px;
  padding: 6px 12px;
  color: #fff;
  position: relative;
  transition: padding 85ms, margin 85ms, box-shadow 85ms;
  width: 87%;
  &:hover {
    box-shadow: -8px 0 rgba(0, 0, 0, 0.2);
    // box-shadow: inset 0 0 0 500px rgba(0, 0, 0, 0.2);
  }
}

.edit-label-btn {
  all: unset;
  border-radius: 3px;
  padding: 6px;
  position: absolute;
  right: 5px;
  top: 0;
  cursor: pointer;
  &:hover {
    background-color: #091e4214;
  }
}

.edit-label-container {
  position: relative;
}


.edit-label-title {
  width: fit-content;
}

.label-check-icon {
  position: absolute;
  left: 210px;
}